* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  color: #5D6063;
  background-color: #EAEDF0;
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.column-select {
  display: flex;
  justify-content: center;
  align-items: center;
}

.select-column {
  cursor: pointer;
  height: 35px;
  overflow: hidden;
  border-radius: 5px;
  font-size: large;
}

.select-int-operators {
  display: flex;
  justify-content: center;
  cursor: pointer;
  height: 35px;
  overflow: hidden;
  border-radius: 5px;
  font-size: large;
}

.select-str-operators {
  display: flex;
  justify-content: center;
  cursor: pointer;
  height: 35px;
  overflow: hidden;
  border-radius: 5px;
  font-size: large;
}

.operator-form {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-form {
  cursor: pointer;
  overflow: hidden;
  border-radius: 5px;
  font-size: large;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.search-form input[type="text"] {
  background-color: #FFFFFF;
  border: 1px solid #D6D9DC;
  border-radius: 3px;
  width: 60%;
  padding: 7px;
  font-size: 16px;
}

.search-form input[type="submit"] {
  background-color: #FFFFFF;
  border: 1px solid #D6D9DC;
  border-radius: 3px;
  width: 60%;
  padding: 7px;
  font-size: 16px;
}

.predicate-start {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  background-color: #F6F7F8;
  border-radius: 6px;
  border: 2px grey solid;
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 3px 0 3px 0;
}

#select {
  font-weight: bold;
  margin-right: 20px;
}

#from {
  font-weight: bold;
  margin-right: 20px;
}

#table {
  margin-right: 20px;
}

#where {
  font-weight: bold;
  margin-right: 20px;
}

#and {
  font-weight: bold;
  margin-right: 20px;
}

#like {
  font-weight: bold;
  margin-right: 10px;
  margin-left: 10px;
}

#in {
  font-weight: bold;
  margin-right: 10px;
  margin-left: 10px;
}


#predicate {
  font-weight: italic;
  margin-right: 20px;
}

.query-builder {
  margin-right: 20px;
  margin-left: 20px
}

.add-line button {
  color: #0099CC;
  background: transparent;
  border: 2px solid #008CBA;
  border-radius: 6px;
  color: black;
  padding: 16px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
}

.add-line button:hover {
  background-color: #008CBA;
  color: white;
}

.result {
  display: flex;
  flex-direction: column;
  margin-top: 20px
}

